* {
  padding: 0;
  margin: 0;
}
.navbar {
  width: 100%;
  height: 80px;
  background-color: #0d253f;
  display: flex;
  justify-content: space-around;
}
.navbar-leftside {
  display: flex;
  align-content: center;
  justify-content: space-between;
  align-items: center;
  width: 40%;
}
.navbar-rightside {
  width: 60%;
  display: flex;
  justify-content: flex-end;
}
.navbar img {
  height: 30px;
  padding: 25px
  
}    
.nav-menu {
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 50vw;
  justify-content: end;
  margin-right: 2rem;
}

.nav-item {
  display: flex;
  align-items: center;
  height: 80px;
}

.nav-links {
  color: white;
  text-decoration: none;
  padding: 0.5rem 1rem;
  font-size: 1em;
}
.nav-links:hover {
  color: #4b9fff;
  transition: .4s;
  cursor: pointer;
}

.nav-links-mobile {
  display: none;
}
.menu-btn {
  position: relative;
  justify-content: center;
  display: none;
  align-items: center;
  height: 80px;
  width: 80px;
  cursor: pointer;
  transition: all .5s ease-in-out;
  /* border: 3px solid white; */
}
.menu-btn_burger {
  height: 4px;
  width: 40px;
  background: white;
  border-radius: 5px;
  transition: all .5s ease-in-out;
}
.menu-btn_burger::before,
.menu-btn_burger::after {
  content: " ";
  position: absolute;
  width: 40px;
  height: 4px;
  background: white;
  border-radius: 5px;
  transition: all .5s ease-in-out;
}
.menu-btn_burger::before {
  transform: translateY(-10px);
}
.menu-btn_burger::after {
  transform: translateY(10px);
}

/* Animation */
.menu-btn.open .menu-btn_burger {
  transform: translateX(-50px);
  background: transparent;
}
.menu-btn.open .menu-btn_burger::before {
  transform: rotate(45deg) translate(35px, -35px);
}
.menu-btn.open .menu-btn_burger::after {
  transform: rotate(-45deg) translate(35px, 35px);
}
.top-container {
  background-image: linear-gradient(to right, #0d253f, #01b4e4);
  background-repeat:inherit;
  background-size: cover;
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  justify-content: center;
  
}

.welcome-h1 {
  margin-top: 20px;
  font-size: 2em;
  padding-left: 30px;
  padding-right: 30px;

  
}
.top-container h2 {
  font-size: 1.2em;
  padding-left: 30px;
  padding-right: 30px;
}
.top-container form {
  width: 100%;
  padding: 50px;
}
.top-container input {
  border-radius: 50px;
  height: 55px;
  width: 85%;
  font-size: 1em;
  padding: 15px;
  margin-left: 30px;
}
.welcome-header-container {
  color: white;
  font-size: 1.5em;
  width: 100%;
  /* padding-left: 40px;
  padding-right: 40px; */
}
.submit-button {
  height: 55px;
  width: 100px;
  border-radius: 50px;
  background: linear-gradient(to right, #90cea1,  #01b4e4);
  color: white;
  font-size: 1em;
  position: relative;
  right: 100px;
  
}

.submit-button:hover {
  color: black;
  transition: .4s;
  
}

/* Welcome Body */

@media screen and (max-width: 960px) {
  .navbar img {
    height: 20px;
    padding: 25px;
  }
  .nav-menu {
    display: block;
    width: 100%;
    height: 250px;
    position: absolute;
    top: 68px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background:rgb(13, 37, 63, .8);
    left: 0;
    top: 68px;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
    display: block;
    
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .menu-btn {
  display: block;
  position: absolute;
  top: -17px;
  right: -79px;
  transform: translate(-100%, 60%);
  font-size: 2.8rem;
  cursor: pointer;
  }

  .nav-links-mobile {
